import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { authAPI } from '../utils/api';
import FormValidator from '../utils/validation';
import {
  AuthContainer,
  AuthCard,
  AuthTitle,
  FormGroup,
  Label,
  Input,
  Button,
  HelperText
} from './styled/AuthStyles';

const Register = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    email: '',
    displayName: ''
  });
  const [errors, setErrors] = useState([]);
  const [fieldErrors, setFieldErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    setFormData(prev => ({ ...prev, [name]: value }));
    
    // Clear field error when user starts typing
    if (fieldErrors[name]) {
      setFieldErrors(prev => ({ ...prev, [name]: null }));
    }
    
    // Real-time validation
    validateField(name, value);
  };

  const validateField = (name, value) => {
    let isValid = true;
    
    switch (name) {
      case 'username':
        isValid = FormValidator.isValidUsername(value);
        setFieldErrors(prev => ({
          ...prev,
          username: isValid ? null : ' 3-50 '
        }));
        break;
      case 'password':
        isValid = FormValidator.isValidPassword(value);
        setFieldErrors(prev => ({
          ...prev,
          password: isValid ? null : ' 6 '
        }));
        break;
      case 'email':
        isValid = FormValidator.isValidEmail(value);
        setFieldErrors(prev => ({
          ...prev,
          email: isValid ? null : ''
        }));
        break;
      case 'displayName':
        isValid = FormValidator.isValidName(value);
        setFieldErrors(prev => ({
          ...prev,
          displayName: isValid ? null : '-'
        }));
        break;
      default:
        break;
    }
    
    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrors([]);
    setSuccessMessage('');
    
    // Validate form
    const validationErrors = FormValidator.validateRegisterForm(formData);
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      return;
    }
    
    setIsLoading(true);
    
    try {
      const result = await authAPI.register(formData);
      
      if (result.success) {
        setSuccessMessage('! ...');
        
        // Redirect to login after 2 seconds
        setTimeout(() => {
          navigate('/login', { 
            state: { 
              message: ' ',
              username: formData.username 
            } 
          });
        }, 2000);
      } else {
        console.error('Registration failed:', result.error);
        setErrors([result.error]);
      }
    } catch (error) {
      console.error('Registration error:', error);
      setErrors([': ' + (error.message || error)]);
    } finally {
      setIsLoading(false);
    }
  };

  const isFormValid = () => {
    return Object.values(formData).every(value => value.trim() !== '') &&
           Object.values(fieldErrors).every(error => error === null);
  };

  return (
    <AuthContainer>
      <AuthCard>
        <AuthTitle></AuthTitle>
        <p style={{ textAlign: 'center', color: '#64748b', marginBottom: '32px' }}>
           Helpdesk
        </p>

        <form onSubmit={handleSubmit}>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px', marginBottom: '16px' }}>
            <FormGroup>
              <Label> *</Label>
              <Input
                type="text"
                name="username"
                value={formData.username}
                onChange={handleInputChange}
                placeholder=""
                required
                minLength={3}
                maxLength={50}
              />
              {fieldErrors.username && (
                <HelperText style={{ color: '#ef4444' }}>
                  {fieldErrors.username}
                </HelperText>
              )}
              <HelperText>3-50 , </HelperText>
            </FormGroup>

            <FormGroup>
              <Label>- *</Label>
              <Input
                type="text"
                name="displayName"
                value={formData.displayName}
                onChange={handleInputChange}
                placeholder="-"
                required
              />
              {fieldErrors.displayName && (
                <HelperText style={{ color: '#ef4444' }}>
                  {fieldErrors.displayName}
                </HelperText>
              )}
            </FormGroup>
          </div>

          <FormGroup>
            <Label> *</Label>
            <Input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleInputChange}
              placeholder="example@email.com"
              required
            />
            {fieldErrors.email && (
              <HelperText style={{ color: '#ef4444' }}>
                {fieldErrors.email}
              </HelperText>
            )}
            <HelperText></HelperText>
          </FormGroup>

          <FormGroup>
            <Label> *</Label>
            <Input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleInputChange}
              placeholder=""
              required
              minLength={6}
            />
            {fieldErrors.password && (
              <HelperText style={{ color: '#ef4444' }}>
                {fieldErrors.password}
              </HelperText>
            )}
            <HelperText> 6 </HelperText>
          </FormGroup>

          {errors.length > 0 && (
            <div style={{
              background: '#fef2f2',
              border: '1px solid #fecaca',
              borderRadius: '8px',
              padding: '12px',
              marginBottom: '16px'
            }}>
              {errors.map((error, index) => (
                <div key={index} style={{ color: '#dc2626' }}>{error}</div>
              ))}
            </div>
          )}

          {successMessage && (
            <div style={{
              background: '#f0fdf4',
              border: '1px solid #bbf7d0',
              borderRadius: '8px',
              padding: '12px',
              marginBottom: '16px',
              color: '#16a34a'
            }}>
              {successMessage}
            </div>
          )}

          <Button 
            type="submit" 
            disabled={!isFormValid() || isLoading}
            style={{ width: '100%', marginBottom: '16px' }}
          >
            {isLoading ? '...' : ''}
          </Button>

          <div style={{ textAlign: 'center' }}>
            <button
              type="button"
              onClick={() => navigate('/login')}
              style={{
                background: 'none',
                border: 'none',
                color: '#3b82f6',
                textDecoration: 'underline',
                cursor: 'pointer'
              }}
            >
              ? 
            </button>
          </div>
        </form>
      </AuthCard>

      {isLoading && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0, 0, 0, 0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 9999
        }}>
          <div style={{
            width: '40px',
            height: '40px',
            border: '4px solid #f3f4f6',
            borderTop: '4px solid #3b82f6',
            borderRadius: '50%',
            animation: 'spin 1s linear infinite'
          }}></div>
        </div>
      )}
    </AuthContainer>
  );
};

export default Register;
